import { useRouter } from 'next/router';
import { useEffect } from 'react';
import { useTranslation } from "@fanfanlo";
import { AppbarContainer, AppShell } from "@app/ui";
import { BasicSettingsContent } from '@src/im/components/room-settings/basic/BasicSettingsContent';

export default function BasicSettingsPage() {
  const { t } = useTranslation("homepage/pages/im/room/room-settings/basic/content");
  const router = useRouter();
  const { id: roomId } = router.query;

  useEffect(() => {
    // 如果没有roomId，重定向到聊天列表
    if (!roomId && router.isReady) {
      router.push('/im');
    }
  }, [roomId, router]);

  console.log('title=', t("content.title"))
  return (
    <AppShell>
      <AppbarContainer appbarProps={{ title: t("content.title"), back: true }}>
        <BasicSettingsContent roomId={roomId as string} />
      </AppbarContainer>
    </AppShell>
  );
} 